import { get, post } from './request'

const HttpManager = {
  // =======================> 用户 API
  // 登录
  loginIn: (params) => post(`user/login/status`, params),
  // 注册
  SignUp: (params) => post(`user/add`, params),
  // 更新用户信息
  updateUserMsg: (params) => post(`user/update`, params),
  // 返回指定ID的用户
  getUserOfId: (id) => get(`user/detail?id=${id}`),

  //获取首页轮播图
  getSwiperList: () => get('/api/www/banner/index/bannerList?httpsStatus=1'),

  // =======================> 排行榜 API
  // 获取首页排行榜
  getIndexBangList: () => get('/api/www/bang/index/bangList?httpsStatus=1'),
  // 获取排行榜类型
  getTagBang: () => get('/api/www/bang/bang/bangMenu?httpsStatus=1'),
  // 获取排行榜歌曲列表，params={bangId(排行榜类型id),pn(页数),rn(数据量)}
  getBangList: (params) => get('/api/www/bang/bang/musicList?httpsStatus=1', params),

  // =======================> 歌单 API
  // 获取首页推荐歌单
  getIndexRcmPlaylist: () => get('/api/www/rcm/index/playlist?httpsStatus=1'),
  // 获取首页歌单类型
  getIndexTagPlaylist: () => get('/api/www/playlist/index/tags?httpsStatus=1'),

  // 获取歌单类型
  getTagPlaylist: () => get('/api/www/playlist/getTagList?httpsStatus=1'),
  // 获取精选歌单，params={order(类型：最新(new)/最热(hot)),pn(页数),rn(数据量)}
  getRcmPlaylist: (params) => get('/api/www/classify/playlist/getRcmPlayList?httpsStatus=1', params),

  // 获取指定类型歌单，params={id(歌单类型id),pn(页数),rn(数据量)}
  getPlaylist: (params) => get('/api/www/classify/playlist/getTagPlayList?httpsStatus=1', params),

  // 获取歌单详细信息，params={pid(歌单id),pn(页数),rn(数据量)}
  getPlaylistInfo: (params) => get('/api/www/playlist/playListInfo?httpsStatus=1', params),

  // 返回标题包含文字的歌单
  getSongListOfLikeTitle: (keywords) => get(`songList/likeTitle/detail?title=${keywords}`),
  // 返回歌单里指定歌单ID的歌曲
  getListSongOfSongId: (songListId) => get(`listSong/detail?songListId=${songListId}`),

  // =======================> 歌手 API
  // 获取首页歌手类型
  getIndexTagSinger: () => get('/api/www/artist/index/tags?httpsStatus=1'),
  // 获取指定类型歌手，params={category(歌手类型id),prefix(可选，姓名首字母，省略表示热门),pn(页数),rn(数据量)}
  getSingerList: (params) => get('/api/www/artist/artistInfo?httpsStatus=1', params),

  getSingerInfo: (id) => get(`/api/www/artist/artist?httpsStatus=1&artistid=${id}`),

  getSingerMusic: (params) => get('/api/www/artist/artistMusic?httpsStatus=1', params),
  getSingerAlbum: (params) => get('/api/www/artist/artistAlbum?httpsStatus=1', params),
  getSingerMv: (params) => get('/api/www/artist/artistMv?httpsStatus=1', params),

  // 通过性别对歌手分类
  getSingerOfSex: (sex) => get(`singer/sex/detail?sex=${sex}`),

  // =======================> 收藏 API
  // 返回的指定用户ID的收藏列表
  getCollectionOfUser: (userId) => get(`collection/detail?userId=${userId}`),
  // 添加收藏的歌曲 type: 0 代表歌曲， 1 代表歌单
  setCollection: (params) => post(`collection/add`, params),

  // =======================> 评分 API
  // 提交评分
  setRank: (params) => post(`rank/add`, params),
  // 获取指定歌单的评分
  getRankOfSongListId: (songListId) => get(`rank?songListId=${songListId}`),

  // =======================> 评论 API
  // 添加评论
  setComment: (params) => post(`comment/add`, params),
  // 点赞
  setLike: (params) => post(`comment/like`, params),
  // 返回所有评论
  getAllComment: (type, id) => {
    let url = ''
    if (type === 1) {
      url = `comment/songList/detail?songListId=${id}`
    } else if (type === 0) {
      url = `comment/song/detail?songId=${id}`
    }
    return get(url)
  },

  // =======================> 歌曲 API
  // 返回指定歌曲ID的歌词信息
  getSongLrc: (id) => get(`/newh5/singles/songinfoandlrc?musicId=${id}&httpsStatus=1`),

  // 返回指定歌曲ID的歌曲信息
  getSongInfo: (id) => get(`/api/www/music/musicInfo?mid=${id}&httpsStatus=1`),
  // 返回指定歌手ID的歌曲
  getSongOfSingerId: (id) => get(`song/singer/detail?singerId=${id}`),
  // 返回指定歌手名的歌曲
  getSongOfSingerName: (keywords) => get(`song/singerName/detail?name=${keywords}`),
  // 下载音乐
  downloadMusic: (url) => get(url, {}, 'blob'),

  // =======================> 专辑 API
  // 返回指定专辑ID的信息,params={albumId(专辑id),pn(页码),rn(数据量)}
  getAlbumInfo: (params) => get('/api/www/album/albumInfo?&httpsStatus=1', params),
  // // 返回指定歌曲ID的歌曲路径
  // getSongUrl: (id) => get(`/api/v1/www/music/playUrl?mid=${id}&type=music&httpsStatus=1`),
  // =======================> 资源 API
  // 返回指定ID的资源路径,params={mid(资源id),type(资源类型：music、mv)}
  getPlayUrl: (params) => get('/api/v1/www/music/playUrl?httpsStatus=1', params),

  // =======================> 搜索 API
  // 获取搜索内容，type=Music、Album、Mv、Playlist、Artist，params={key(搜索值),pn(页数),rn(数据量)}
  getSearchByKeyWord: (type, params) => get(`/api/www/search/search${type}BykeyWord?httpsStatus=1`, params),
  // 获取搜索值推荐
  getSearchKey: (keyword) => get(`/api/www/search/searchKey?key=${keyword}&httpsStatus=1`),

}

export { HttpManager }



