import song from "./song"
import configure from "./configure"

import Vuex from "vuex";
import Vue from "vue";
Vue.use(Vuex)
const store = new Vuex.Store({
  modules: {
    song: song,
    configure: configure
  }
})


if (module.hot) {
  module.hot.accept(['./song'], () => {
    store.hotUpdate({
      modules: { song: require('./song').default }
    })
  })
}
export default store;



