const song = {
  state: {
    lrcList: [],
    isLrc: 0,
    isPlay: false,
    curTime: 0,
    songInfo: {},
    songUrl: '',
    playSong: -1,
  },
  getters: {
    isLrc: state => state.isLrc,
    isPlay: state => state.isPlay,
    playSong: state => state.playSong,
    lrcList: state => {
      let lrcList = state.lrcList
      if (!lrcList) {
        lrcList = JSON.parse(window.sessionStorage.getItem('lrcList') || null)
      }
      return lrcList
    }
    ,
    curTime: state => {
      let curTime = state.curTime
      if (!curTime) {
        curTime = JSON.parse(window.sessionStorage.getItem('curTime') || null)
      }
      return curTime
    }
    ,
    songInfo: state => {
      let songInfo = state.songInfo
      if (!songInfo) {
        songInfo = JSON.parse(window.sessionStorage.getItem('songInfo') || null)
      }
      return songInfo
    }
    ,
    songUrl: state => {
      let songUrl = state.songUrl
      if (!songUrl) {
        songUrl = JSON.parse(window.sessionStorage.getItem('songUrl') || null)
      }
      return songUrl
    }
    ,
  },
  mutations: {
    setIsPlay (state, isPlay) {
      state.isPlay = isPlay;
    },
    setLrcList (state, lrcList) {
      state.lrcList = lrcList;
    },
    setIsLrc (state, isLrc) {
      state.isLrc = isLrc;
    },
    setCurTime (state, curTime) {
      state.curTime = curTime;
    },
    setSongInfo (state, songInfo) {
      state.songInfo = songInfo;
    },
    setSongUrl (state, songUrl) {
      state.songUrl = songUrl;
    },
    setPlaySong (state, playSong) {
      state.playSong = playSong;
    },
  }
}

export default song



