<template>
  <div id="app">
    <!--    <img src="./assets/logo.png">-->
    <keep-alive>
      <music-header></music-header>
    </keep-alive>
    <!-- keep-alive 缓存页面 -->
    <keep-alive>
      <router-view class="container" :key="$route.fullPath"></router-view>
    </keep-alive>
    <music-footer></music-footer>
    <!-- 在mv播放界面不显示 -->
    <music-play v-if="$route.name!='mvPlay'"></music-play>

  </div>
</template>

<script>
import MusicFooter from './components/MusicFooter.vue'
import MusicHeader from './components/MusicHeader.vue'
import MusicPlay from './components/MusicPlay.vue'
document.cookie = "kw_token=EO83VCKQTKF; path=/" //设置cookie和路径
export default {
  components: { MusicHeader, MusicFooter, MusicPlay },
  name: 'App',
}

</script>

<style>
#app {
  min-height: 100vh;
  position: relative;
}
/* .header {
  border-bottom: 1px solid #f0f0f0;
} */
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.container {
  min-width: 990px;
  max-width: 1490px;
  padding: 0 120px;
  margin: 0 auto;
  /* min-height: calc(100vh - 200px); */
}
</style>



