<!--  -->
<template>
  <div>
    <div class="list_head">
      <ul class="flex_c">
        <li class="head_num">序号</li>
        <li class="head_name">歌曲</li>
        <li class="head_artist">歌手</li>
        <li class="head_album">专辑</li>
        <li class="head_time">时长</li>
      </ul>
    </div>
    <div class="song_list">
      <ul>
        <template v-for="(item,index) in songList">
          <li class="song_item flex_c" :key="index">
            <div class="song_rank">
              <div class="rank_num">
                <span class="playing" v-show="playSong===item.rid">
                  <span class="side1" :class="isPlay?'playing_now':'pause'"></span>
                  <span class="side2" :class="isPlay?'playing_now':'pause'"></span>
                  <span class="side3" :class="isPlay?'playing_now':'pause'"></span>
                </span>
                <span v-show="playSong!=item.rid">{{(pageNum-1)*30+index+1}}</span>
              </div>
              <!-- <img class="cover" loading="lazy" :src="item.pic||item.img||'noimg.jpg'" @error="imgError($event)"> -->
            </div>
            <div class="song_name flex_c">
              <span :title="item.name.split('-')[0]" class="name" v-html="item.name.split('-')[0]" @click="goPage('music',item.rid)"></span>
            </div>
            <div class="song_artist">
              <span :title="item.artist" v-html="item.artist" @click="goPage('singer',item.artistid)"></span>
            </div>
            <div class="song_album">
              <span :title="item.album" v-html="item.album" @click="goPage('album',item.albumid)"></span>
            </div>
            <div class="song_time">
              <span>{{item.songTimeMinutes}}</span>
            </div>
            <div class="song_opts flex_c">
              <i class="iconfont icon-icon_play_" @click="playBtnClick(item.rid,index)"></i>
              <!-- <i class="iconfont icon-playlist_icon_add_"></i> -->
              <i class="iconfont icon-bar_icon_heart_"></i>
              <i class="iconfont icon-bar_icon_download_"></i>
              <slot name="opts"></slot>
            </div>
          </li>
        </template>
      </ul>
    </div>
  </div>
</template>

<script>
import mixin from '../mixins';
import { mapGetters } from 'vuex';
import { HttpManager } from '../api';
import Storage from '../storage';
export default {
  name: "MusicTable",
  mixins: [mixin],
  props: {
    songList: Array,
    playFunc: {
      type: Function,
    },
    pageNum: {
      type: Number,
      default: 1,
    },
  },
  data () {
    return {
      // isPlay: false,
      // isPlaySong: -1,
    };
  },

  components: {},

  methods: {
    // goPlayPage (rid) {
    //   this.$router.push({ path: '/songDetail/' + rid })
    // },
    // goAlbumPage (aid) {
    //   this.$router.push({ path: '/albumDetail/' + aid })
    // },
    // goArtistPage (id) {
    //   this.$router.push({ path: '/singerDetail/' + id })
    // },
    // 对象数组去重
    unique (arr) {
      const res = new Map();
      // 对象中rid唯一
      return arr.filter((arr) => !res.has(arr.rid) && res.set(arr.rid, 1));
    },
    //播放按钮点击函数
    playBtnClick (rid, index) {
      this.$store.commit('setPlaySong', rid);
      // 判断传递的函数是否存在，不存在则执行默认函数
      if (typeof this.playFunc == 'function') {
        // 设置播放歌曲在列表中的位置
        Storage.setLocalStorage('play_song_index', index);
        //播放列表的播放按钮点击函数，获取列表中歌曲信息且不改变歌曲顺序
        this.playFunc(index);
      } else {
        Storage.setLocalStorage('play_song_index', 0);
        this.getSongInfo(rid);
      }
    },
    getSongInfo (rid) {
      let that = this;
      //暂停播放
      that.$store.commit('setIsPlay', false);
      HttpManager.getSongInfo(rid)
        .then((res) => {
          let songList;
          // that.code = parseInt(res.code);
          that.$store.commit('setSongInfo', res.data);
          // 加入local Storage 的播放列表
          if (Storage.getLocalStorage('song_list') === null) {
            songList = [];
          } else {
            songList = Storage.getLocalStorage('song_list');
          }
          songList.unshift(res.data);
          Storage.setLocalStorage('song_list', this.unique(songList));
        })
        .catch(() => { });
      HttpManager.getPlayUrl({ mid: rid, type: 'music' })
        .then((res) => {
          // that.status = parseInt(res.status);
          that.$store.commit('setSongUrl', res.data.url);

          that.$store.commit('setIsPlay', true);
        })
        .catch(() => { });
      console.log(Storage.getLocalStorage('song_list'))
    },
  },

  computed: {
    ...mapGetters([
      'isPlay',
      'playSong',
    ])
  },

  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../assets/css/font";

*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}

li {
  list-style: none;
}
.song_list {
  .song_item {
    .song_rank {
      width: 60px;
      // .cover {
      //   margin-left: 15%;
      // }
    }
    .song_name {
      width: 29%;
    }

    .song_artist {
      width: 25%;
    }

    .song_time {
      width: 8%;
    }
  }
}
.list_head {
  height: 46px;
  line-height: 46px;
  background: #f9f9f9;
  color: #999;

  font-size: 14px;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;

  .head_num {
    width: 60px;
    padding-left: 18px;
    // min-width: 100px;
  }

  .head_name {
    width: 29%;
    padding-left: 18px;
  }

  .head_artist {
    width: 25%;
  }

  .head_album {
    flex: 1;
  }

  .head_time {
    width: 8%;
  }
}
.song_item {
  height: 70px;
  line-height: 22px;
  color: #666;

  font-size: 14px;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  &:nth-child(2n) {
    background: #f8f8f8;
  }

  &:hover {
    background: #f5f8fb;
    .song_time {
      display: none;
    }

    .song_opts {
      width: 20%;
      padding-left: 4.28%;
      display: flex;
      justify-content: space-between;
    }

    .song_opts .iconfont {
      font-size: 20px;
      cursor: pointer;
    }
  }

  &.current {
    background: rgba(0, 0, 0, 0.05);
    .song_name {
      font-weight: 600;
    }
  }
  .song_rank {
    // position: relative;

    width: 60px;
    // min-width: 100px;
    // padding-right: 20px;

    .rank_num {
      flex-shrink: 0;
      width: 20px;
      height: 33px;
      color: #333;
      line-height: 33px;
      text-align: center;
      font-weight: 700;
      margin-left: auto;
      margin-right: auto;
    }

    .rank_num.top1 {
      background: url(https://h5static.kuwo.cn/www/kw-www/img/icon_first.df06fb1.png)
        center 4px no-repeat;
      background-size: 168%;
    }

    .rank_num.top2 {
      background: url(https://h5static.kuwo.cn/www/kw-www/img/icon_secondary.d99e73e.png)
        center 4px no-repeat;
      background-size: 168%;
    }

    .rank_num.top3 {
      background: url(https://h5static.kuwo.cn/www/kw-www/img/icon_third.8abbb65.png)
        center 4px no-repeat;
      background-size: 168%;
    }

    // .cover {
    //   flex-shrink: 0;
    //   width: 54px;
    //   height: 54px;
    // }
  }
  .song_name {
    width: 26.83%;
    padding-right: 2.43%;
    padding-left: 18px;
    color: #333;
    span {
      cursor: pointer;
    }

    .name {
      flex-shrink: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    a {
      border: none;
      text-decoration: none;
      &.name {
        cursor: pointer;
        color: #333;
      }
    }
    .type {
      margin-left: 5px;
      flex-shrink: 0;
      font-size: 15px;
      color: #e6b86d;
      cursor: pointer;
    }

    .has_mv {
      margin-left: 5px;
      flex-shrink: 0;
      font-size: 21px;
      cursor: pointer;
      color: #666;
    }
  }
  .song_artist {
    width: 17.82%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 1%;
    span {
      cursor: pointer;
    }
  }
  .song_album {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 6px;
    span {
      cursor: pointer;
    }
  }
  .song_time {
    width: 6.56%;
    padding-right: 3.93%;
  }

  .song_opts {
    margin-right: 3.93%;
    width: 200px;
    display: none;
  }

  &.noCopyright {
    .song_name,
    .song_name a.name,
    .song_rank .rank_num {
      color: rgba(0, 0, 0, 0.4);
    }
  }
}
.playing {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  line-height: 14px;
  text-align: center;
  span {
    position: absolute;
    bottom: 0;
    width: 2px;
    background: #31c27c;
  }
}

@-webkit-keyframes first {
  0% {
    height: 6px;
  }

  56% {
    height: 10px;
  }

  to {
    height: 8px;
  }
}
@keyframes first {
  0% {
    height: 6px;
  }

  56% {
    height: 10px;
  }
  to {
    height: 8px;
  }
}
.side1 {
  left: 2px;
  height: 6px;
  -webkit-animation: first 1s linear infinite;
  animation: first 1s linear infinite;
}
@-webkit-keyframes second {
  0% {
    height: 4px;
  }

  28% {
    height: 6px;
  }

  80% {
    height: 12px;
  }

  to {
    height: 6px;
  }
}

@keyframes second {
  0% {
    height: 4px;
  }

  28% {
    height: 6px;
  }

  80% {
    height: 12px;
  }

  to {
    height: 6px;
  }
}
.side2 {
  left: 6px;
  height: 4px;
  -webkit-animation: second 1s linear infinite;
  animation: second 1s linear infinite;
}
@-webkit-keyframes middle {
  0% {
    height: 10px;
  }

  56% {
    height: 8px;
  }

  to {
    height: 6px;
  }
}
@keyframes middle {
  0% {
    height: 10px;
  }

  56% {
    height: 8px;
  }
  to {
    height: 6px;
  }
}
.side3 {
  left: 10px;
  height: 6px;
  -webkit-animation: middle 1s linear infinite;
  animation: middle 1s linear infinite;
}
.pause {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}
.playing_now {
  -webkit-animation-play-state: running;
  animation-play-state: running;
}

.flex_c {
  display: flex;
  align-items: center;
}
</style>



