import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: '*',
      redirect: '/404'
    },
    {
      path: '/404',
      component: () => import('@/pages/404')
    },
    {
      path: "/",
      name: "home",
      component: () => import('@/pages/Home'),
    },
    {
      path: "/rankList",
      name: "rankList",
      component: () => import('@/pages/RankList'),
    },
    {
      path: "/singers",
      name: "singers",
      component: () => import('@/pages/Singers'),
    },
    {
      path: "/playlists",
      name: "playlists",
      component: () => import('@/pages/Playlists'),
    },
    {
      path: "/playlistDetail/:pid",
      name: "playlistDetail",
      component: () => import('@/pages/detail/PlaylistDetail'),
    },
    {
      path: "/albumDetail/:aid",
      name: "albumDetail",
      component: () => import('@/pages/detail/AlbumDetail'),
    },
    {
      path: "/singerDetail/:id",
      name: "singerDetail",
      component: () => import('@/pages/detail/SingerDetail'),
    },
    {
      path: "/songDetail/:rid",
      name: "songDetail",
      component: () => import('@/pages/detail/SongDetail'),
    },
    {
      path: "/mvPlay/:id",
      name: "mvPlay",
      component: () => import('@/pages/MvPlay'),
    },
    {
      path: "/search",
      name: "search",
      component: () => import('@/pages/Search'),
      children: [
        {
          path: "song",
          name: "searchSong",
          component: () => import('@/pages/search/SearchSong'),
        }, {
          path: "album",
          name: "searchAlbum",
          component: () => import('@/pages/search/SearchAlbum'),
        }, {
          path: "mv",
          name: "searchMv",
          component: () => import('@/pages/search/SearchMv'),

        }, {
          path: "playlist",
          name: "searchPlaylist",
          component: () => import('@/pages/search/SearchPlaylist'),
        },
        {
          path: "singer",
          name: "searchSinger",
          component: () => import('@/pages/search/SearchSinger'),
        },
      ]
    },
  ],
  // 跳转或刷新页面自动回顶部
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    }
    return {
      x: 0,
      y: 0
    }
  }
});



